.about {
    background-image: linear-gradient(160deg, $purple, $bg);
    padding-top: 5px;
    overflow-x: hidden;

    gap: 30px;

    .about-box {
        width: 375px;

        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 30px;

        .my-words {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 15px;
            width: 375px;

            padding: 30px 15px;

            p {
                font-size: rem(17px);
                text-align: center;
                line-height: 1.35;
            }
        }

        .contact-box {
            display: flex;
            justify-content: center;
            flex-grow: 1;
            background-color: $bg;
            width: 550px;

            .img-box {
                align-self: flex-end;
                width: 175px;
                // transform: translate(-50%, 3px);

                img {
                }
            }

            .contact {
                margin: 0 15px;
                // width: 125px;
                // transform: translateX(-40%);
                align-self: center;
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 15px;

                p {
                    text-align: center;
                    line-height: 1.35;
                }

                h5 {
                    font-size: rem(20px);
                    font-weight: 300;
                }

                svg {
                    box-shadow: $white-shadow;
                    margin-top: 10px;
                    height: 36px;
                }
            }
        }
    }
}
